<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import CommunicationEventService from '@/services/nucleus/CommunicationEventService'
import EnumerationService from '@/services/commons/EnumerationService'
import StatusItemService from '@/services/commons/StatusItemService'
import PartyService from '@/services/nucleus/PartyService'
import PartyLookup from '@/views/nucleus/party/PartyLookup'
import { FormWizard, TabContent } from 'vue-form-wizard'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'communication-event-create',
  mixins: [AlertMixin, DatePickerMixin],
  components: {
    FormWizard,
    TabContent,
    PartyLookup,
    Datepicker
  },
  data: function () {
    return {
      communicationEventTypeId: 'FaceToFace',
      communicationEventTypes: [],
      contactMechTypeEnumId: '',
      contactMechTypes: [],
      statusId: '',
      statusItems: [],
      reasonEnumId: '',
      reasons: [],
      fromPartyId: '',
      toPartyId: '',
      startDate: '',
      endDate: '',
      subject: '',
      body: '',
      note: '',

      fromPartySwitch: false,
      toPartySwitch: false,
      fromGender: 'M',
      fromGenderSwitch: false,
      fromSalutation: '',
      fromFirstName: '',
      fromMiddleName: '',
      fromLastName: '',
      fromSuffix: '',
      fromMothersMaidenName: '',
      fromBirthDate: '',
      fromDeceasedDate: '',
      fromMaritalStatusEnumId: 'MarsSingle',
      fromEmploymentStatusEnumId: 'EmpsFullTime',
      fromResidenceStatusEnumId: 'RessOwn',
      fromOccupation: '',
      toGender: 'M',
      toGenderSwitch: false,
      toSalutation: '',
      toFirstName: '',
      toMiddleName: '',
      toLastName: '',
      toSuffix: '',
      toMothersMaidenName: '',
      toBirthDate: '',
      toDeceasedDate: '',
      toMaritalStatusEnumId: 'MarsSingle',
      toEmploymentStatusEnumId: 'EmpsFullTime',
      toResidenceStatusEnumId: 'RessOwn',
      toOccupation: '',
      maritalStatuses: [],
      employmentStatuses: [],
      residenceStatuses: []
    }
  },
  methods: {
    async load () {
      let self = this
      CommunicationEventService.communicationEventTypes({
        orderByField: 'description'
      }).then(function (response) {
        self.communicationEventTypes = response.data.eventTypeList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'ContactMechType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.contactMechTypes = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'CommunicationPurpose',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.reasons = response.data.enumerationList || []
      })

      StatusItemService.statusItems({
      }).then(function (response) {
        self.statusItems = response.data.statusItemList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'MaritalStatus',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.maritalStatuses = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'ResidenceStatus',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.residenceStatuses = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'EmploymentStatus',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.employmentStatuses = response.data.enumerationList || []
      })
    },
    async createCommunicationEvent (nextAction) {
      let self = this
      if (this.fromPartySwitch) {
        await PartyService.createParty({
          gender: this.fromGender,
          salutation: this.fromSalutation,
          firstName: this.fromFirstName,
          middleName: this.fromMiddleName,
          lastName: this.fromLastName,
          suffix: this.fromSuffix,
          mothersMaidenName: this.fromMothersMaidenName,
          birthDate: this.fromBirthDate,
          deceasedDate: this.fromDeceasedDate,
          maritalStatusEnumId: this.fromMaritalStatusEnumId,
          residenceStatusEnumId: this.fromResidenceStatusEnumId,
          employmentStatusEnumId: this.fromEmploymentStatusEnumId,
          occupation: this.fromOccupation
        }).then(function (response) {
          self.fromPartyId = response.data.partyId
        }).catch(function (error) {
          self.showDangerAlert(error.response.data.errors)
        })
      }

      if (this.toPartySwitch) {
        await PartyService.createParty({
          gender: this.toGender,
          salutation: this.toSalutation,
          firstName: this.toFirstName,
          middleName: this.toMiddleName,
          lastName: this.toLastName,
          suffix: this.toSuffix,
          mothersMaidenName: this.toMothersMaidenName,
          birthDate: this.toBirthDate,
          deceasedDate: this.toDeceasedDate,
          maritalStatusEnumId: this.toMaritalStatusEnumId,
          residenceStatusEnumId: this.toResidenceStatusEnumId,
          employmentStatusEnumId: this.toEmploymentStatusEnumId,
          occupation: this.toOccupation
        }).then(function (response) {
          self.toPartyId = response.data.partyId
        }).catch(function (error) {
          self.showDangerAlert(error.response.data.errors)
        })
      }

      CommunicationEventService.createCommunicationEvent({
        communicationEventTypeId: this.communicationEventTypeId,
        contactMechTypeEnumId: this.contactMechTypeEnumId,
        statusId: this.statusId,
        fromPartyId: this.fromPartyId,
        toPartyId: this.toPartyId,
        startDate: this.startDate,
        endDate: this.endDate,
        subject: this.subject,
        body: this.body,
        note: this.note,
        reasonEnumId: this.reasonEnumId
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
            self.$refs.wizard.reset()
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewCommunicationEvent', params: { id: response.data.communicationEventId } })
          } else {
            self.$router.push({ name: 'BrowseCommunicationEvents' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    selectFromPartyRecord (record) {
      this.fromPartyId = record.partyId || ''
    },
    selectToPartyRecord (record) {
      this.toPartyId = record.partyId || ''
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  },
  watch: {
    fromGenderSwitch: function () {
      this.fromGender = this.fromGenderSwitch ? 'F' : 'M'
    },
    toGenderSwitch: function () {
      this.toGender = this.toGenderSwitch ? 'F' : 'M'
    }
  }
}
</script>
